<template>
  <label class="form-input flex--c">
    <input
      :id="id"
      :type="modal"
      :readonly="!!readonly"
      @focus="onFocus"
      @blur="onBlur"
      :placeholder="placeholder"
      v-model="val"
      :maxlength="maxLength"
      :style="{ color: !isFocus && warning ? '#FF4D4F' : '' }"
    />
    <i
      class="icon-suffix clear"
      v-show="val && isFocus && !readonly"
      @mousedown.prevent="
        val = '';
        $emit('clear');
      "
    ></i>
    <slot name="warning" :data="{ readonly, val, isFocus, warning }">
      <i
        class="icon-suffix warning"
        v-show="!isFocus && warning && !readonly"
      ></i>
    </slot>
    <slot name="readonly" :data="{ readonly, val, isFocus, warning }"></slot>
  </label>
</template>

<script>
export default {
  name: 'form-input',
  model: {
    prop: 'value',
    event: 'input',
  },
  props: {
    id: String,
    // 占位符
    placeholder: String,
    // 输入框类型
    modal: String,
    // 最大长度
    maxLength: String,
    readonly: null,
    value: null,
    // 是否显示警告
    warning: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isFocus: false,
    };
  },
  computed: {
    val: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit('input', val);
      },
    },
  },
  methods: {
    onFocus() {
      this.$emit('focus', this.val);
      this.isFocus = true;
    },
    onBlur() {
      this.$emit('blur', this.val);
      this.isFocus = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.form-input {
  flex: 1;
  align-self: center;
  height: 21px;
  line-height: 1;

  input {
    border: 0;
    color: #333;
    width: 100%;
    background: #fff;
    font-size: 16px;

    &[readonly] {
      color: #999;
    }

    &::-webkit-input-placeholder {
      color: #ccc;
    }
    &::-moz-placeholder {
      /* Mozilla Firefox 19+ */
      color: #ccc;
    }
    &:-moz-placeholder {
      /* Mozilla Firefox 4 to 18 */
      color: #ccc;
    }
    &:-ms-input-placeholder {
      /* Internet Explorer 10-11 */
      color: #ccc;
    }
  }
  .icon-suffix {
    width: 22px;
    height: 22px;
    // margin-right: 30px;
    background-repeat: no-repeat;
    background-position: center;
    flex-shrink: 0;
  }
  .warning {
    background-image: url(data:image/png;base64,);
    background-size: 22px 22px;
  }

  .clear {
    background-image: url(data:image/png;base64,);
    background-size: 27px 27px;
  }
}
</style>
