<script>
export default {
  name: 'form-radio',
  props: {
    checked: null,
    value: null,
    activeClass: {
      type: [String, Array, Object],
      default: 'active',
    },
  },
  data() {
    return {};
  },
  created() {
    if (this.checked && this.value !== this.$parent.value) {
      this.$parent.setValue(this.value);
    }
  },
  methods: {
    onChoose() {
      this.$parent.setValue(this.value);
    },
  },
  render() {
    const checked = this.$parent.value === this.value;
    return (
      <div
        onClick={this.onChoose}
        class={['c-form-radio', 'flex-c-c', checked ? this.activeClass : '']}
      >
        {this.$scopedSlots.default({ checked })}
      </div>
    );
    // 使用this.$scopedSlots可以支持插槽作用域
  },
};
</script>

<style lang="scss">
.form-radio-group {
  flex: 1;
  display: flex;
  flex-wrap: wrap;

  .c-form-radio {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #333;
    border: 1px solid #ccc;
    box-shadow: inset 0 0 0 1px rgba(204, 204, 204, 1);
    height: 36px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    padding: 0 5px;
    box-sizing: border-box;
    cursor: pointer;

    &.active {
      color: #ff6600;
      border-color: transparent;
      box-shadow: inset 0 0 0 1px #ff6600;
      background-image: url(data:image/png;base64,);
      background-position: right top;
      background-repeat: no-repeat;
      background-size: 18px 18px;
    }
  }
}
</style>
